//这是store中从components文件中引进的data.js文件以下是需要添加的代码
export default ({
	state:{     //数据书写
		a:'aaaa'
	},
	mutations:{     //mutations中方法书写
		add(state){
			state.a+='test'
		}
	},
	actions:{
		add2(state){
			if(1>0){   //actions中有判断方法的书写
				state.commit('add')
			}
		}
	},
	getters:{      //获取变化后的数据
		add3(state){
			return state.a+'getters'
		}
	}
	
})



